"use client";
import { useState, useEffect } from "react";
import Link from "next/link";
import Image from "next/image";
import { usePathname } from "next/navigation";
import { X } from "lucide-react";

export default function Navbar() {
    const [isSticky, setIsSticky] = useState(false);
    const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false);
    const pathname = usePathname();

    useEffect(() => {
        const onScroll = () => {
            setIsSticky(window.scrollY > 50);
        };
        window.addEventListener("scroll", onScroll);
        return () => window.removeEventListener("scroll", onScroll);
    }, []);

    // Close mobile menu when route changes
    useEffect(() => {
        setIsMobileMenuOpen(false);
    }, [pathname]);

    // Prevent scrolling when mobile menu is open
    useEffect(() => {
        if (isMobileMenuOpen) {
            document.body.style.overflow = 'hidden';
        } else {
            document.body.style.overflow = 'unset';
        }
    }, [isMobileMenuOpen]);

    const headerClass = `site-header ${isSticky ? "is-sticky" : ""}`;

    const navLinks = [
        { href: "/", label: "HOME" },
        { href: "/livescore", label: "LIVESCORE" },
        { href: "/livestream", label: "LIVESTREAM" },
        { href: "/news", label: "NEWS" },
        { href: "/highlight", label: "HIGHLIGHTS" },
    ];

    return (
        <header className={headerClass}>
            {/* Main Header Content */}
            <div className="header-container header-container--rounded header-container--large relative z-20">
                <div className="header-filter"></div>
                <div className="header-overlay"></div>
                <div className="header-specular"></div>
                <div className="header-content relative">
                    <Link href="/" className="header__logo">
                        <Image
                            src="https://sharesport.news/logo/1675071036-logo.jpg"
                            alt="Share Sports Logo"
                            width={40}
                            height={40}
                        />
                        <h2>Share Sport</h2>
                    </Link>
                    <div className="header__nav-wrapper">
                        <nav className="header__nav">
                            {navLinks.map((link) => (
                                <Link
                                    key={link.href}
                                    href={link.href}
                                    className={`header__nav-link ${
                                        pathname === link.href ? "is-active" : ""
                                    }`}
                                >
                                    {link.label}
                                </Link>
                            ))}
                        </nav>
                        <button
                            className="header__mobile-menu-btn lg:hidden z-50 relative ml-4"
                            aria-label="Toggle mobile menu"
                            onClick={() => setIsMobileMenuOpen(!isMobileMenuOpen)}
                        >
                            {isMobileMenuOpen ? (
                                <X className="icon h-8 w-8 [#4c3b71]" />
                            ) : (
                                <svg
                                    xmlns="http://www.w3.org/2000/svg"
                                    fill="none"
                                    viewBox="0 0 24 24"
                                    strokeWidth={1.5}
                                    stroke="currentColor"
                                    className="icon h-8 w-8 [#4c3b71]"
                                >
                                    <path
                                        strokeLinecap="round"
                                        strokeLinejoin="round"
                                        d="M3.75 6.75h16.5M3.75 12h16.5m-16.5 5.25h16.5"
                                    />
                                </svg>
                            )}
                        </button>
                    </div>
                </div>
            </div>

            {/* Dropdown Mobile Menu - Glass Effect Applied */}
            <div
                className={`absolute top-full left-0 w-full bg-white/70 backdrop-blur-xl border-t border-white/40 shadow-xl transition-all duration-300 ease-in-out origin-top z-10 lg:hidden ${
                    isMobileMenuOpen
                        ? "opacity-100 scale-y-100 visible"
                        : "opacity-0 scale-y-0 invisible"
                }`}
            >
                <nav className="flex flex-col">
                    {navLinks.map((link) => (
                        <Link
                            key={link.href}
                            href={link.href}
                            className={`block w-full text-left px-6 py-4 border-b border-gray-200/50 text-sm font-bold tracking-wider uppercase transition-colors ${
                                pathname === link.href
                                    ? "bg-white/50 text-[#4c3b71] border-l-4 border-l-[#4c3b71]"
                                    : "text-gray-700 hover:bg-white/40 hover:text-[#4c3b71]"
                            }`}
                            onClick={() => setIsMobileMenuOpen(false)}
                        >
                            {link.label}
                        </Link>
                    ))}
                </nav>
            </div>
        </header>
    );
}